@import '../../global.scss';

.clients{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 80%;
    margin: 0 auto;
   

    .item{
         position: relative;
        width: 10rem;
        height: 10rem;
        border-radius: 50%;
        border: 5px solid var(--white);
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        .inner{
           display: flex;
           align-items: center;
           justify-content: center;
           flex-direction: column;

           h2{
               font-size: 2rem;
               color: var(--white);
           }
           p{
               font-size: 1.25rem;
               color: var(--white);
           }
        }

        &.active{
                border-color: rgb(138, 136, 136);
                background: aliceblue;

                .inner{
                    h2{
                        color: var(--second-color);
                    }
                    p{
                        color: var(--main-color);
                    }
                }
            
        }
        
    }
}
