@import '../../global.scss';


.about{
  
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .top{
         h3{
              color: var(--second-color);
          }
          .heading{
              color: var(--main-color);

              &::after{
                  background-color: var(--second-color);
              }
          }
    }

    .bottom{
          background-color: white;
        z-index: 2;
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: center;

        .left{
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            padding: 1rem;
            h2{
                font-weight: 600;
                color: var(--second-color);
                margin-bottom: .5rem;
                font-size: 1.2rem;
            }

            p{
                color: var(--main-color);
            }
            .bars{
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
                margin: 1rem 0 .75rem 0;

                .bar{
                    margin: .5rem 0;
                    width: 100%;
                    display: flex;
                    align-items: flex-start;
                    justify-content: center;
                    flex-direction: column;
                    span{
                        width: 90%;
                        height: 5px;
                        background: var(--grad);

                    }
                }
            }

        }
        .right{
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;

            img{
                width: 100%;
            }
        }

    }
    img{
        width: 10%;
    }
    .pat-left{
        position: absolute;
        left: 0;
        bottom: 10%;
        z-index: 1;
    }
     .pat-right{
        position: absolute;
        right: 0;
        bottom: 10%;
        z-index: 1;
    }
}