@import '../../global.scss';

.services{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .top{
          h3{
              color: var(--second-color);
          }
          .heading{
              color: var(--main-color);

              &::after{
                  background-color: var(--second-color);
              }
          }
    }
    .bottom{
        margin: 3rem auto;
        width: 80%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        .item{
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin: .75rem;
            background: aliceblue;
            padding: 2rem 1rem;
            transition: all .5s ease-in-out;
            z-index: 2;
            border-radius: .75rem;

            h3{
                margin: .5rem 0;
            }
            p{
                text-align: center;
                margin: .5rem 0;
            }

            img{
                width: 50%;
            }

            &:hover{
                border-bottom: 5px solid var(--second-color);
                transform: translateY(-5%);
                cursor: pointer;
            }
        }
    }

    .left-pat{
        width: 10%;
        position: absolute;
        left: 5rem;
        bottom: 5%;
    }
     .right-pat{
        width: 10%;
        position: absolute;
        right: 5rem;
        bottom: 5%;
    }
}



@media screen and (max-width:768px) {
  
      

        .container{
            width: 100%;
            
            .top{
                width: 100%;
            }
            .bottom{
               flex-direction: column;
            }
          }
     }
 

@media screen and (max-width:640px) {
  
      

        .container{
            width: 100%;
            
            .top{
                width: 100%;

                h1{
                    font-size: 2rem;
                }
                h3{
                    font-size: 1rem;
                }
            }
            .bottom{
               flex-direction: column;
            }
          }
     }
 
