@import '../../global.scss';

.topbar{
    position: absolute;
    padding: 0 7.9rem;
    width: 100%;
    height: 4.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    

    .navbar{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo{
            text-decoration: none;
            font-size: 2rem;
            font-weight: 600;
            color: var(--third-color);
        }
        .right{
         ul{
            list-style-type: none;
            display: flex;
            align-items: center;
            justify-content: center;
           

            li{
                // font-size: 1rem;
                text-transform: uppercase;

                

                a{
                   color: var(--second-color); 
                   text-decoration: none;
                   margin: 0 .75rem;
                   transition: 1s;

                   &.current{
                       background:var(--grad);
                       padding: .5rem;
                       color: var(--white);
                       border-radius: .75rem 5rem 1rem 2rem;

                       &:hover{
                           color: var(--white);
                       }
                   }

                   &:hover{
                       color: var(--third-color);
                   }
                }
               
            }
        }

        .hamburger{
            position: absolute;
            width: 30px;
            height: 25px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            top: 50%;
            transform: translateY(-50%);
            right: 2.4rem;
            cursor: pointer;
            opacity: 0;
            z-index: 500;
          

            span{
                width: 100%;
                height: 3px;
                background: var(--third-color);
                transition: 2s;
            }
        }
      }
    }
}

@media screen and (max-width:900px) {
    .topbar{
        z-index: 1500;
        padding:  3.4rem;
     
        transition: all 2s ease;
        
        .navbar{
            .right{
                ul{ position: fixed;
                    padding-left: 1.5rem;
                    top: 0rem;
                    left: 0;
                    width: 70%;
                    height: 70vh;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;
                    background-color: rgb(21, 28, 36);
                    transition: all 1s ease;
                    transform: translateX(-100%);
                    z-index: 1000;
                    

                    li{
                        margin: .75rem 0;
                        font-size: 1.01rem;
                        color: var(--white);
                        a{
                            color: inherit;
                        }
                    }
                }
                .hamburger{
                    opacity: 1;
                }
            }
        }

        &.active{
            // background-color: black;
            
            .navbar{
                .right{
                    ul{
                        transform: translateX(0);
                    }
                    .hamburger{
                            
                        span{
                            
                            transform-origin: left;
                            transition: all 2s ease;
                            &:first-child{
                                transform: rotate(45deg);
                            }
                            &:nth-child(2){
                                opacity: 0;
                            }
                            &:last-child{
                                transform: rotate(-45deg);
                            }
                        }
                    }
                }
            }

        }
    }
}