@import './global.scss';


section{
    padding: 5rem 7.9rem;
}

.portfolioSection{
   width: 100%;
   display: flex;

    background: linear-gradient( #642B73 50%, white 50% );
}

.clientSection{
    background: linear-gradient(to top right, #642B73, #C6426E);
}


.contactSection{
    padding: 0;
    margin: 0;
    position: relative;
    background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTyB2hBJb3wgZ3gsS1QEdx0jxeaQY7gtjXHuA&usqp=CAU');
    

    .overlay{
        width: 100%;
        background: linear-gradient(transparent 50%, #642B73 50% );
        padding: 5rem 7.9rem;
    }
}

.footerSection{
    background-color: var(--main-color);
    color: aliceblue;
}


@media screen and (max-width:900px) {
    section{
        padding: 3.4rem;
        
    }
   
}

@media screen and (max-width:768px) {
    .contactSection{
      
      .overlay{
          padding: 3.4rem;

        .container{
            width: 100%;
            padding: 1rem;
          }
     }
   }
}

@media screen and (max-width:640px) {
  section{
      .container{
                    h1{
      font-size: 2rem;
      text-align: center;
       }
       p{
      font-size: 1rem;
  }
          .top{
                h1{
      font-size: 1.8rem;
       }
  h3{
      font-size: 1rem;
  }
  p{
      font-size: 1rem;
  }
          }
      }
  }
}

