@import '../../global.scss';


.blog{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   

    .top{
         h3{
              color: var(--second-color);
          }
          .heading{
              color: var(--main-color);

              &::after{
                  background-color: var(--second-color);
              }
          }
    }
    
    .bottom{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 3rem;

        .item{
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin: .75rem;
            background: aliceblue;
            border-radius: 1rem;
            padding: 2rem 1rem;
            transition: all .5s ease-in-out;

            .image{
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 1rem ;
                
                

                img{
                    width: 100%;
                }
            }
            .title{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: .75rem;

                h3{
                    font-size: 1.1rem;
                    color: var(--second-color);
                }

                h4{
                    font-size: 1.01rem;
                    color: var(--third-color);
                }
            }
            p{
                
                margin-bottom: 1.25rem;
            }

            a{
                text-decoration: none;
                font-size: 1.01rem;
                color: var(--second-color);
            }

            &:hover{
                transform: translateY(-5%);
                border-bottom: 5px solid var(--second-color);
            }
        }
    }
}