@import '../../global.scss';

.testimonial{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

     .top{
         h3{
              color: var(--second-color);
          }
          .heading{
              color: var(--main-color);

              &::after{
                  background-color: var(--second-color);
              }
          }
    }
    
    .bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        
        .left{
            position: relative;
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            .star{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                margin: .5rem 0;

                span{
                    &>*{
                        color: rgb(206, 158, 1) ;
                    }
                }
            }
            p{
                margin-bottom: 1rem;
                 color: var(--main-color);
            } 
            h2{
                font-weight: 400;
                color: var(--main-color);
            } 
            h3{
                font-size: 1.1rem;
                color: var(--second-color);
            }
            .arrow{
                position: absolute;
                right: 3rem;
                bottom: 0;


                &>*{
                    font-size: 2.5rem;
                    width: 3rem;
                    color: var(--third-color);
                    cursor: pointer;
                }
            }          

        }

        .right{
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            img{
                width: 100%;
            }
        }
    }
}