@import '../../global.scss';

.hero{
    display: flex;
    align-items: center;
    justify-content: center;
    
    .left{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        h2{
            color: var(--main-color);
            font-size: 1.5rem;
            margin-bottom: .5rem;
            &:first-child{
                color: var(--second-color);
                font-weight: 500;
            }
            
            span{
                color: var(--third-color);
            }
        }
        .hero-name{
            font-size: 3rem;
            color: var(--main-color);
            margin: .2rem 0;
        }
       

    }
    .right{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        img{
            max-width: 100%;
            width: 80%;
        }
    }
}

@media screen and (max-width:900px) {
    .container{
       width: 100%;
       padding-top: 5rem;
    }
}
   
@media screen and (max-width:768px) {
    .container{
       width: 100%;
       padding-top: 5rem;

       .right{
           align-items: center;
           justify-content: center;
           margin: 1rem 0 0 0;
       }
    }
}
   

