@import '../../global.scss';

.hire-me{
    width: 60%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1{
        position: relative;
        color: var(--main-color);
        font-size: 2.5rem;
        margin: .75rem 0;

        &::after{
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 4rem;
            height: 4px;
            background: var(--second-color);
            border-radius:.75rem 5rem 1rem 2rem ;
            bottom: 0;

        }
    }
    p{
        text-align: center;
        padding: .75rem;
    }

}