*,
::before,
::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body{
    font-family: 'Lato', sans-serif;
    font-size: 12px;
}

:root{
    --main-color: #272727;
    --grad:  linear-gradient(to right, #642B73, #C6426E);
    --second-color: #642B73;
    --third-color: #C6426E;
    --white:aliceblue;
}

//GLOBAL 

//button
.global-btn{
            text-decoration: none;
            margin: .75rem 0;
            background: var(--grad);
            padding: .5rem .75rem;
            color: var(--white);
            border-radius: .75rem 5rem 1rem 2rem; 
            transition: background-color .5s ease-in-out;
            
            &:hover{
                background: var(--main-color);
            }
}
//top
 .top{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h3{
          color: inherit; 
        }

        .heading{
            position: relative;
            font-size: 2.5rem;
            margin-bottom: .5rem;

            &::after{
                content: '';
                position: absolute;
                width: 4rem;
                height: 4px;
                background-color: inherit;
                left: 50%;
                transform: translateX(-50%);
                bottom: .2rem;
                border-radius:.75rem 5rem 1rem 2rem ;

            }
        }
        p{
            text-align: center;
        }
        
    }
section{
    // padding: 0rem 7.9rem;
    margin: 0 auto;
}
p{
    font-size: 1.01rem;
}

@media screen and (max-width:900px) {
    section{
      

        .container{
        width: 100%;
        flex-wrap: wrap;
        
    }
    }
   
}

@media screen and (max-width:768px) {
   
      

        .container{
        width: 100%;
        flex-direction: column;
        flex-wrap: wrap;
            
          .top{
              width: 80%;

          }
          .bottom{
              flex-direction: column;
              width: 100%;
          }

        }
    }
   


