@import '../../global.scss';

.contact{
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 0px 15px -8px black;
    padding: 3rem;
     border-radius:.75rem 5rem 1rem 2rem ;
     overflow: hidden;
     background: #fff;
     z-index: 2000;

    .left{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        width: 100%;
        padding-right: 3rem;

         .contact-top{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
           
            

             .heading{
              color: var(--main-color);
              font-size: 2.5rem;
              position: relative;

              &::after{
                  position: absolute;
                  width: 4rem;
                  content: '';
                  height: 4px;
                  left: 50%;
                  transform: translateX(-50%);
                  bottom: 0;
                  bottom: .2rem;
                  border-radius:.75rem 5rem 1rem 2rem ;
                  background-color: var(--second-color);
                }
            }
            p{
             margin: 1rem 0;
            }

        }
        .bottom{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;

            p{
                color: var(--main-color);
                display: flex;
                align-items: center;
                margin: .5rem 0;

                span{
                    width: 2.2rem;
                    height: 2.2rem;
                    background-color: var(--second-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: .5rem;
                    border-radius:.75rem 3rem 1rem 2rem ;
                    padding: .2rem;

                    &>*{
                        color: var(--white);
                    }
                }
            }
        }

        
    }
    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        overflow: hidden;

        h2{
            font-size: 2rem;
            color: var(--second-color);
            font-weight: 400;
        }

        form{
            width: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            overflow: hidden;

            input{
                width: 100%;
                padding: .2rem .5rem;
                background-color: transparent;
                border: none;
                border-bottom: 1px solid rgba(0,0,0,.3);
                margin: .6rem 0;
                outline: none;
                font-size: 1rem;
                font-weight: 500;
                &:last-child{
                    border-bottom: none;
                    background: var(--grad);
                    color: var(--white);
                    transition: background-color 1s ease-in-out;
                    width: 80%;
                    padding: .75rem;
                    cursor: pointer;
                      border-radius:.75rem 3rem 1rem 2rem;
                      margin-top: 1rem; 
                      
                        &:hover{
                        background: var(--main-color);
                    }
                }
               
            }
            textarea{
                width: 100%;
                border: 1px solid rgba(0,0,0,.3);
                outline: none;
                padding: .2rem .5rem;
                font-size: 1rem;
                font-family: 'Lato';
                font-weight: 500;
                background: transparent;
            }

        }
        .output{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
        }

    }
}

@media screen and (max-width:768px) {
   
      
      .overlay{
          padding: 3.4rem;

        .container{
            width: 100%;
            padding: 1rem;

            .left{
                margin: 1rem;
            }

            .right{
                margin: 1rem 0;
                form{
                    width: 100%;
                    padding-right: 1rem;
                }
            }
          }
     }
   }

   @media screen and (max-width:450px) {
   
      
      .overlay{
          padding: 3.4rem;

        .container{
            width: 100%;
            padding: 1rem;
            .left{
                .contact-top{
                    h1{
                    font-size: 2rem;
                }
                }
               
            }

            .right{
                form{
                    width: 100%;
                }
            }
          }
     }
   }

 

