@import '../../global.scss';
section{

.portfolio{
    
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .top{
        margin-top: -2rem;
        h3{
            color: var(--white);
        }
        .heading{
            color: var(--white);

            &::after{
                background-color: var(--white);
            }
        }
    }
    .bottom{
        width: 100%;
        flex-direction: column;
        display: flex;
        align-items: center;
        justify-content: center;

        ul{
            list-style: none;
            width: 60%;
            margin: 2rem auto;
            display: flex;
            align-items: center;
            justify-content: space-between;

            li{
                font-size: 1rem;
                text-transform: uppercase;
                color: var(--white);
                margin: .75rem 1rem;
                cursor: pointer;

                &.active{
                    background: var(--white);
                    color: var(--second-color);
                    padding: .3rem .5rem;
                    border-radius: .75rem 5rem 1rem 2rem;  
                }
            }

        }
         .content{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
        .item{
            max-width: 250px;
            display: flex;
            align-items: center;
            justify-content: center;
           position: relative;
           object-fit: contain;
           transition: 1s;
           border-radius: .5rem;
           border: 2px solid var(--third-color);
           cursor: pointer;
           margin: .5rem;
        


            img{
            width: 100%;
            z-index: 2;
            border-radius: .5rem;
            }
            h3{
                color: var(--white);
                font-size: 1.2rem;
                position: absolute;
            }
              &:hover{
                background: rgba(0,0,0,.8);

                img{
                opacity: .4;
              }
              h3{
                  z-index: 3;
              }
            }
            

          
        }


       
    }
    }
   
}
}


@media screen and (max-width:768px){
    .portfolio{
        width: 100%;
        .bottom{
              width: 100%;
            ul{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;

                li{
                    font-size: 0.9rem;
                    margin: .2rem;
                }
            }
          

           
        }
    }
}