@import '../../global.scss';

.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .footer-top{
        display: flex;
        width: 100%;
        align-items: flex-start;
        justify-content: flex-start;

        .item{
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;

            p{
                margin: .2rem 0;
                &:first-child{
                    font-weight: 600;
                    margin-bottom: .5rem;
                    font-size: 1.1rem;
                }
            }
        }
        .links{
            margin-left: 7rem;

            ul{
                list-style: none;
                li{
                    font-size: 1.01rem;
                    margin: .25rem 0;

                    a{
                        text-decoration: none;
                        color: inherit;
                    }
                }
            }
        }
        .services{
            margin-right: 2rem;
        }
        .suscribe{
           form{
               margin-top: 1.5rem;
               display: flex;
               align-items: center;
               justify-content: center;

               input{
                   padding: .25rem .5rem;
                   font-size: 1.01rem;
                   background: rgba(255,255,255,.1);
                   border: none;
                   outline: none;
                   color: inherit;
                   border-radius: .5rem 0 0 .5rem;
               }
               button{
                   background: var(--second-color);
                   border-radius: 0 .5rem .5rem 0;
                   border: none;
                   cursor: pointer;

                   &>*{
                       width: 1.5rem;
                       color: var(--white);
                   }
               }
           } 
        }
    }

    .bottom{
         margin-top: 4rem;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            flex: 1;
            display: flex;
            align-items: flex-start;
            

            &>*{
                margin-right: .5rem;
            }

            p{
                &:last-child{
                    color: var(--third-color);
                }
            }
        }
        .right{
           
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            p{
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: rem;
            }
            ul{
                margin-right: 3rem;
                list-style: none;
                display: flex;
                align-items: center;
                li{
                    margin: 0 .125rem;

                    &>*{
                        color: var(--third-color);
                        transition: all 1s ease-in-out;

                        &:hover{
                            color: var(--white);
                        }
                    }
                }
            }
            .to-top{
                width: 2rem;
                height: 2rem;
                position: absolute;
                right: 0;
                background-color: var(--second-color);
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                cursor: pointer;

               &>*{
                   color: var(--white);
                   transition: all 2s ease;
                   animation: up 2s infinite;
               }
            }
        }
    }

    @keyframes up {
        0%{
            opacity: .1;
        }
        50%{
            transform: translateY(-10%);
        }
        100%{
            opacity: 1;
        }
    }
}


@media screen and (max-width:900px) {
    section{
      

        .container{
        width: 100%;
        flex-wrap: wrap;
        
    }
    }
   
}

@media screen and (max-width:768px) {
    section{
      

        .container{
        width: 100%;
        flex-direction: column;
       
            
          .footer-top{
              width: 100%;
              flex-wrap: wrap;

              &>*{
                  margin: .75rem;
              }

              .links{
                  margin-left: 0;
              }
              .servises{
                  margin-right: 0;
              }


          }
          .bottom{
              flex-direction: column;
              flex-wrap: wrap;
              align-items: center;
              justify-content: center;
              width: 100%;

              .left{
                  margin: 1rem;
              }
          }

        }
    }
   
}


  @media screen and (max-width:450px) {
   
      
        .contact{
         

           .container{
                .footer-top{
                    flex-direction: column;
                    flex-wrap: nowrap;
                    align-items: center;
                    justify-content: center;

                    .links{
                        margin-left: 2rem;
                    }
                }
            }
        }
    
}